import uniCalendar from '@/components/uni-calendar/uni-calendar.vue'

export default {
	components: {
		uniCalendar
	},

	data() {
		/**
		 * 时间计算
		 */
		function getDate(date, AddDayCount = 0) {
			if (typeof date !== 'object') {
				date = date.replace(/-/g, '/');
			}
			let dd = new Date(date);
			dd.setMonth(dd.getMonth() + AddDayCount); // 获取AddDayCount天后的日期
			let y = dd.getFullYear();
			let m = dd.getMonth() + 1 < 10 ? '0' + (dd.getMonth() + 1) : dd.getMonth() + 1; // 获取当前月份的日期，不足10补0
			let d = dd.getDate() < 10 ? '0' + dd.getDate() : dd.getDate(); // 获取当前几号，不足10补0
			return y + '-' + m + '-' + d;
		}
		let tags = {
			lunar: {
				name: '农历',
				checked: false,
				attr: 'lunar'
			},
			fixedHeihgt: {
				name: '固定高度',
				checked: false,
				attr: 'fixedHeihgt'
			},
			vertical: {
				name: '垂直滚动',
				checked: false,
				attr: 'vertical'
			},
			horizontal: {
				name: '水平滚动',
				checked: false,
				attr: 'horizontal'
			},
			startDate: {
				name: '开始日期(' + getDate(new Date(), -1) + ')',
				checked: false,
				value: getDate(new Date(), -1),
				attr: 'startDate'
			},
			endDate: {
				name: '结束日期(' + getDate(new Date(), 2) + ')',
				value: getDate(new Date(), 2),
				checked: false,
				attr: 'endDate'
			},
			disableBefore: {
				name: '禁用今天之前的日期',
				checked: false,
				attr: 'disableBefore'
			},
			date: {
				name: '自定义当前日期(' + getDate(new Date(), 1) + ')',
				value: getDate(new Date(), 1),
				checked: false,
				attr: 'date'
			}
		};


		return {
			show: false,
			tags,
			slide: 'none',
			date: '',
			startDate: '',
			endDate: '',
			timeData: {}
		};
	},
	onLoad() {},
	methods: {
		closeMask() {
			this.show = false;
		},
		toggle(index, item) {
			this.tags[index].checked = !item.checked;
			// item.checked = !item.checked;
			if (index === 'horizontal') {
				this.tags['vertical'].checked = false;
			}
			if (index === 'vertical') {
				this.tags['horizontal'].checked = false;
			}
			// this.attribute[item.attr] = !item.checked;
		},
		open() {
			if (this.tags['horizontal'].checked) {
				this.slide = 'horizontal';
			} else if (this.tags['vertical'].checked) {
				this.slide = 'vertical';
			} else {
				this.slide = 'none';
			}
			if (this.tags['startDate'].checked) {
				this.startDate = this.tags['startDate'].value;
			} else {
				this.startDate = '';
			}
			if (this.tags['endDate'].checked) {
				this.endDate = this.tags['endDate'].value;
			} else {
				this.endDate = '';
			}
			if (this.tags['date'].checked) {
				this.date = this.tags['date'].value;
			} else {
				this.date = '';
			}
			this.show = true;
			console.log(this.date)
		},
		change(e) {
			console.log('change 返回:', e.fulldate);
			this.timeData = e;
		},
		toClick(e) {
			console.log('点击事件', e.fulldate);
			this.timeData = e;
		},
		confirm() {
			this.show = false;
		}
	}
};
